/** Colors **/
/** Component sizes, margins, padding **/
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  color: #B3B5BC;
  text-decoration: none; }

.sidenav---sidenav---_2tBP {
  background-color: #005361; }

.sidenav---sidenav-toggle---1KRjR {
  height: 56px;
  width: 64px; }

::-webkit-scrollbar {
  display: none; }

/** Colors **/
/** Component sizes, margins, padding **/
.navbar {
  background-color: #F1F2F7 !important; }

a {
  color: #B3B5BC !important; }

#logo-container {
  padding-left: 20px; }

#logo {
  text-transform: uppercase;
  padding: 10px 20px;
  margin-top: 8px;
  border: 1px solid #41C3A1;
  color: #F1F2F7;
  float: left;
  cursor: pointer; }

a:hover {
  text-decoration: none;
  color: #41C3A1; }

.large-menu, .small-menu {
  text-align: right; }

#sidebar {
  width: 20vw;
  float: left;
  background-color: #F1F2F7;
  height: calc(100vh - 60px); }

#sidebar * {
  padding: 25px; }

#panel {
  width: 40vw;
  background-color: #F1F2F7;
  height: 100vh;
  float: right; }

#panel * {
  padding: 25px; }

#panel-illustration {
  display: -webkit-flex;
  display: flex;
  height: 60vh; }

#panel-illustration > img {
  width: 75%;
  margin: auto; }

#content {
  height: calc(100vh - 60px);
  overflow-y: scroll; }

#content-next-to-panel {
  width: calc(100vw - 40vw);
  height: 100vh;
  overflow-y: scroll; }

.MuiToolbar-gutters-48 {
  padding: 0 !important; }

.MuiListItem-gutters {
  padding-left: 30px !important; }

main {
  position: relative; }

.expanded {
  margin-left: 240px; }

.not-expanded {
  margin-left: 64px; }

.next-to-sidebar {
  overflow-y: scroll;
  height: calc(100vh - 56px); }

.dark > a {
  color: #B3B5BC !important; }

.light > a {
  color: #005361 !important; }

.dark {
  background-color: #005361 !important; }

.light {
  background-color: #B3B5BC !important; }

/** Colors **/
/** Component sizes, margins, padding **/

h1 {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 50;
  margin: 30px 0;
  letter-spacing: 1px; }

p {
  margin: 50px 130px;
  text-align: justify; }

